export default {
	data() {
		return {
			flag: true,
			user_machine: '',
			machine_id: '',
			user_agreement: '',
			shade: false
		};
	},
	onShow() {
		uni.request({
			url: this.url + 'usermachine/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success: res => {
				console.log(res);
				this.user_machine = res.data.data.machine_datas;
				if (this.user_machine.length == 0) {
					this.flag = false;
				}
				//else {
				// 	this.flag = false
				// }
				this.user_agreement = res.data.data.user_agreement;
				if (this.user_agreement == 0) {
					this.shade = true;
				} else if (this.user_agreement == 1) {
					this.shade = false;
				}
				console.log(this.user_agreement);
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		sure: function() {
			uni.navigateTo({
				url: '../../pages/agreement/agreement'
			});
		},
		cancel: function() {
			uni.navigateBack({
				url: '../../pages/my/my'
			});
		},
		onNavigationBarButtonTap: function() {
			uni.navigateTo({
				url: '../../pages/agreement/agreement'
			});
		},

		select: function(item) {
			uni.navigateTo({
				url: '../machine-detail/machine-detail?machine_id=' + item.machine_id
			});
		}
	}
};
